.page-footer {
  background-color: @eucalyptus;
  color: @white;
  font-size: 16px;
  line-height: 26px;
  padding-top: 52px;
}

.page-footer__inner_bg-dark {
  background-color: @green-pea;
}

.page-footer__wrapper {
  display: flex;

  .container(@desktop-width);
}

.page-footer__column {
  display: flex;
  flex-direction: column;
  width: 33.33%;
  padding-right: 15px;
  padding-left: 15px;
}

.page-footer__title {
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 28px;
}

.page-footer__list {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 10px;
  list-style: none;
}

.page-footer__item {
  margin-bottom: 12px;

}

.page-footer__link {
  color: @white;
  text-decoration: none;
  display: inline-block;

  &_social {
    position: relative;
    padding-left: 82px;
    line-height: 50px;
  }

  &_social::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 42px;
    height: 42px;
    background-image: url("../img/social-sprites.png");
    background-repeat: no-repeat;
  }

  &_social-facebook::before {
    background-position: 0 0;
  }

  &_social-twitter::before {
    background-position: -150px 0;
  }

  &_social-vk::before {
    background-position: -200px 0;
  }

  &_social-ok::before {
    background-position: -100px 0;
  }

  &_social-instagram::before {
    background-position: -50px 0;
  }

  &_color-gray::before {
    color: @silver;
  }
}

@media (min-width: @touch-width) {
  .page-footer__link{
    &:hover,
    &:focus {
      opacity: 0.7;
      text-decoration: underline;
    }

    &:active {
      opacity: 0.3;
      text-decoration: underline;
    }
  }
}

@media (max-width: 1024px) {
  .page-footer {
    font-size: 14px;
  }

  .page-footer__list_social {
    display: flex;
    flex-direction: row;
  }

  .page-footer__link_social {
    font-size: 0;
    padding-left: 44px;
    transform: scale(0.8);
    transform-origin: left center;
  }
}

@media (max-width: @tablet-width) {
  .page-footer__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .page-footer__column {
    width: 100%;
    margin-bottom: 16px;
  }

  .page-footer__link_social {
    transform: scale(0.7);
    padding-left: 36px;
  }

  .page-footer__title {
    margin-bottom: 12px;
  }

  .page-footer__item {
    margin-bottom: 6px;
  }

  .page-footer__info {
    margin: 0;
  }

  .page-footer__inner_bg-dark {
    padding-top: 24px;
    padding-bottom: 8px;
  }
}
