.product {
  display: flex;
  padding: 60px 15px 170px;
  font-size: 16px;

  .container(@desktop-width);
}

.product__image {
  width: 300px;
  height: 320px;
  margin-right: 110px;
  overflow: hidden;
  flex: none;

  img {
    max-width: 100%;
    object-fit: cover;
  }
}

.product__title {
  margin-top: 17px;
  margin-bottom: 4px;
  font-size: 20px;
  font-weight: 600;
  line-height: 26px;
  text-transform: uppercase;
}

.product__subtitle {
  margin-top: 0;
  margin-bottom: 24px;
  font-size: 16px;
  font-weight: 400;
  color: @ironside-gray;
  font-style: italic;
}

.product__subject {
  color: @cod-gray;
  letter-spacing: 0.16px;
  line-height: 26px;
  font-weight: bold;
}

.product__option {
  margin: 0;

  &:not(:last-child) {
    margin-bottom: 12px;
  }

  &_style-italic {
    font-style: italic;
  }
}

.product__text {
  color: @ironside-gray;
  font-weight: 300;
  letter-spacing: 0.16px;
  line-height: 18px;
}

.product__price {
  color: @forest-green;
  font-size: 30px;
  font-weight: 600;
  line-height: 26px;
  margin-top: 55px;
  margin-bottom: 20px;
}

.product__button {
  border: 1px solid @supernova;
  background-color: @supernova;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 600;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 0.12px;
  color: @cod-gray;
  width: 190px;
  height: 35px;
}

@media (min-width: @touch-width) {
  .product__button {
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: fade(@supernova, 70%);
      border-color: fade(@supernova, 50%);
    }

    &:active {
      background-color: fade(@supernova, 30%);
      border-color: fade(@supernova, 10%);
    }
  }
}

@media (max-width: @tablet-width) {
  .product {
    flex-direction: column;
    align-items: center;
  }

  .product__image {
    margin-right: 0;
  }
}
