.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 60px;
  margin-bottom: 40px;
  padding-right: 15px;
  padding-left: 15px;
  list-style: none;

  .container(@desktop-width);
}

.breadcrumb__item {
  position: relative;
  display: inline-flex;

  &:not(:last-child) {
    padding-right: 20px;
  }

  &:not(:last-child)::after {
    content: "/";
    position: absolute;
    right: 9px;
    top: 50%;
    transform: translateY(-50%);
    align-self: flex-start;
  }
}

.breadcrumb__link {
  display: inline;
  color: @sushi;
  text-decoration: none;
}

@media (min-width: @touch-width) {
  .breadcrumb__link {
    cursor: pointer;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }
}
