html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  outline: none;
}

body {
  font-family: "ProximaNova", "Arial", sans-serif;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.page-header {
  background-color: #ffffff;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.18);
}

.page-header__wrapper {
  padding: 10px 15px;
