  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  color: #000000;
  text-decoration: none;
}

.page-header__cart::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -35px;
  width: 30px;
  height: 30px;
  transform: translateY(-50%);
  background-image: url("../img/header-cart.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 18px;
}

.page-header__user {
  position: relative;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  color: #000000;
  text-decoration: none;
}

.page-header__user::before {
  content: "";
