.page-header {
  background-color: @white;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.18);
}

.page-header__wrapper {
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .container(@desktop-width);
}

.page-header__logo {
  display: flex;
  padding-right: 48px;
}

.page-header__cart {
  position: relative;
  margin-right: 56px;
  margin-left: auto;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  color: @black;
  text-decoration: none;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -35px;
    width: 30px;
    height: 30px;
    transform: translateY(-50%);
    background-image: url("../img/header-cart.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto 18px;
  }
}

.page-header__user {
  position: relative;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  color: @black;
  text-decoration: none;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -35px;
    width: 30px;
    height: 30px;
    transform: translateY(-50%);
    background-image: url("../img/header-user.png");
  }
}

@media (min-width: @touch-width) {
  .page-header__cart {
    cursor: pointer;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }

  .page-header__user {
    cursor: pointer;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

@media (max-width: 576px) {
  .page-header__cart,
  .page-header__user {
    font-size: 0;

  }

  .page-header__cart {
    margin-right: 46px;
  }
}

