.pagination {
  display: flex;
  justify-content: center;
}

.pagination__list {
  height: 32px;
  padding-left: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;
}

.pagination__item {
  &:not(:last-child) {
    margin-right: 10px;
  }
}

.pagination__link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: @black;
  background-color: @polcelain;
  font-size: 14px;
  font-weight: 600;
  line-height: 26px;
  width: 32px;
  height: 100%;

  &_navigation {
    width: 80px;
    border: 2px solid @polcelain;
    background-color: @white;
  }

  &_active {
    background-color: @sushi;
    color: @white;
  }

  &_disable {
    color: @dove-gray;
  }
}

@media (min-width: @touch-width) {
  .pagination__link {
    &:not(.pagination__link_active):not(.pagination__link_disable) {
      &:hover,
      &:focus {
        background-color: darken(@polcelain, 5%);
      }

      &:active {
        background-color: lighten(@polcelain, 2%);
      }
    }
  }
}

@media (max-width: 425px) {
  .pagination__list {
    flex-wrap: wrap;
  }

  .pagination__link_navigation {
    font-size: 0;
    width: 32px;
  }

  .pagination__link_prev {
    background-image: url("../../img/left-arrow.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 40% 40%;
  }

  .pagination__link_next {
    background-image: url("../../img/right-arrow.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 40% 40%;
  }
}
