.logo {
  flex: initial;
  object-fit: contain;
  object-position: center;
  display: flex;
  align-items: center;

  &:not(:last-child) {
    margin-right: 60px;
  }

  img {
    max-width: 100%;
    object-fit: contain;
  }
}


@media (max-width: @tablet-width) {
  .logo {
    max-width: 320px;


    &:not(:last-child) {
      margin-right: 12px;
    }
  }
}
