.filter {
  display: flex;
  padding: 14px 15px;
  margin-bottom: 9.5vw;
  background-color: @supernova;

  .container(1410px); //  ,    
}

.filter__list {
  display: flex;
  flex: auto;
  justify-content: space-around;
  align-items: center;
  padding-left: 0;
  margin: 0;
  list-style: none;
}

.filter__item {
  padding-right: 10px;
  padding-left: 10px;
}

.filter__link {
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 0.14px;
  text-decoration: none;
  color: @black;
}

.filter__title,
.filter__toggle {
  display: none;
}

@media (min-width: @touch-width) {
  .filter__link {
    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

@media (max-width: @tablet-width) {
  .filter {
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-bottom: 100px;
  }

  .filter__list {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    flex-direction: column;
    background: lighten(@supernova, 10%);
    align-items: flex-start;
    padding-left: 28px;
    overflow: hidden;
    transform: scaleY(0);
    transform-origin: center top;
    transition: transform 0.15s ease;
    z-index: 5;
  }

  .filter__title,
  .filter__toggle {
    display: flex;
    font-weight: 600;
  }

  .filter__title {
    text-transform: uppercase;
  }

  .filter__toggle {
    align-items: center;

    &.is-active + .filter__list {
      transform: scaleY(1);
      transition: transform 0.15s ease;
    }
  }

  .filter__item {
    width: 100%;
  }

  .filter__link {
    display: inline-flex;
    width: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
  }
}
