.toggle {
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
}

.toggle:hover,
.toggle:focus {
  outline: none;
}

.toggle.is-active .toggle__inner,
.toggle.is-active .toggle__inner::before,
.toggle.is-active .toggle__inner::after {
  background-color: @cod-gray;
}

.toggle__box {
  width: 32px;
  height: 32px;
  display: inline-block;
  position: relative;
}

.toggle__inner {
  display: block;
  top: 50%;
  margin-top: -2px;
}

.toggle__inner, .toggle__inner::before, .toggle__inner::after {
  width: 30px;
  height: 3px;
  background-color: @cod-gray;
  border-radius: 3px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.toggle__inner::before, .toggle__inner::after {
  content: "";
  display: block;
}

.toggle__inner::before {
  top: -10px;
}

.toggle__inner::after {
  bottom: -10px;
}

.toggle .toggle__inner {
  top: 6px;
}

.toggle .toggle__inner::before {
  top: 10px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}

.toggle .toggle__inner::after {
  top: 20px;
}

.toggle.is-active .toggle__inner {
  transform: translate3d(0, 10px, 0) rotate(45deg);
}

.toggle.is-active .toggle__inner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
}

.toggle.is-active .toggle__inner::after {
  transform: translate3d(0, -20px, 0) rotate(-90deg);
}
