.order-form__main {
  display: flex;
}

.order-form__main-item {
  width: 50%;

  h2 {
    margin-bottom: 32px;
    font-size: 20px;
    font-weight: 600;
    line-height: 26px;
    text-transform: uppercase;
  }
}

.order-form__top {
  h2 {
    margin-top: 40px;
    margin-bottom: 8px;
    font-size: 20px;
    font-weight: 600;
    line-height: 26px;
    text-transform: uppercase;
  }
}

.order-form__field {
  display: flex;
  margin-bottom: 26px;

  .order-form__label {
    font-weight: normal;
    text-transform: uppercase;
    margin-top: 7px;
    font-size: 16px;
    margin-bottom: 5px;
    line-height: 1.42857143;
    color: #333333;
    width: 33.33333333%;
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
  }

  .order-form__input,
  .order-form__textarea {
    resize: none;
    width: 50%;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555555;
    background-color: #ffffff;
    background-image: none;
    border: 1px solid #cccccc;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    height: auto;
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    border-radius: 0;
  }

  span {
    font-weight: normal;
    text-transform: uppercase;
    margin-top: 7px;
    font-size: 16px;
    margin-bottom: 5px;
    line-height: 1.42857143;
    color: #333333;
  }

  i {
    display: block;
  }
}

.order-form__phone,
.order-form__date {
  width: 50%;

  .order-form__input {
    width: 100%;
  }

  .iti {
    width: 100%;
  }
}

.order-form__date {
  .order-form__input {
    border: none;
    font-weight: normal;
    text-transform: uppercase;
    font-size: 16px;
    margin-left: 3px;
    letter-spacing: 1px;
    box-shadow: none;
  }
}


.order-form {
  max-width: 1440px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 100px;
  padding-right: 15px;
  padding-left: 15px;

  .error {
    color: #a94442;
    background-color: #f2dede;
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid transparent;
    border-radius: 4px;
    margin-top: 20px;
  }
}

.order-form__city {
  width: 50%;

  a {
    outline: none;
    color: @supernova;
    font-weight: 600;
    text-decoration: underline;
    display: inline-block;
    margin-top: 7px;
    font-size: 16px;
    line-height: 1.42857143;

    &:hover {
      text-decoration: none;
    }
  }
}

.order-form__input-container {

  margin-top: 7px;
  width: 100%;
  align-items: center;
  display: none;

  .order-form__input {
    width: 85%;
  }

  .order-form__close {
    font-size: 21px;
    font-weight: 700;
    line-height: 21px;
    color: #000000;
    text-shadow: 0 1px 0 #ffffff;
    filter: alpha(opacity=20);
    opacity: .2;
    cursor: pointer;
    width: 15%;
    justify-content: center;
    align-items: center;
    display: flex;
    transition: all 0.2s;

    &:hover {
      opacity: 1;
    }
  }
}


.order-form__notes {
  width: 50%;

  .order-form__input {
    width: 100%;
  }

  .order-form__notes-description {
    font-size: 13px;
    line-height: 1.42857143;
    color: #333333;
    font-style: italic;
    text-transform: none;
    margin-top: 5px;
    display: block;
  }
}

.order-form__confirm {
  margin-top: 17px;
  width: 100%;
  margin-bottom: 40px;
}

.order-form__confirm-row {
  width: 100%;
  display: flex;
  height: 65px;
  align-items: center;
  padding: 0 50px;
  border-bottom: 1px solid @supernova;

  .order-form__confirm-name,
  .order-form__confirm-price,
  .order-form__confirm-del {
    span {
      font-size: 17px;
      color: #030303;
      text-transform: uppercase;
      font-weight: normal;
    }
  }

  .order-form__confirm-del {
    padding-left: 20px;
    cursor: pointer;
    opacity: 0.5;
    transition: all 0.2s;

    &:hover {
      opacity: 1;
    }
  }

  .order-form__confirm-price {
    span {
      font-weight: normal;
      color: @forest-green;
    }
  }
}

.order-form__confirm-row_heading {
  background: @supernova;
  height: 53px;
  border-bottom: none;

  .order-form__confirm-name,
  .order-form__confirm-price,
  .order-form__confirm-del {
    span {
      font-size: 17px;
      color: #030303;
      text-transform: none;
      pointer-events: none;
    }
  }

  .order-form__confirm-del {
    padding-left: 0;
    opacity: 1;
    pointer-events: none;
  }
}

.order-form__confirm-name {
  width: 50%;
}

.order-form__confirm-price {
  width: 40%;
}

.order-form__confirm-del {
  width: 10%;
}

.order-form__total {
  display: flex;
  margin-bottom: 31px;

  > div {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }


}

.order-form__discount {
  display: flex;
  align-items: center;
  padding-left: 50px;

  .order-form__label {
    font-size: 20px;
    font-weight: normal;
    margin-top: 7px;
    margin-left: 6px;
    margin-bottom: 5px;
  }

  .order-form__input {
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555555;
    background-color: #ffffff;
    background-image: none;
    border: 1px solid #cccccc;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    resize: none;
    margin-left: 17px;
    border-radius: 0;
  }

  button {
    width: 200px;
    height: 34px;
    border: 1px solid #ffcb06;
    background-color: #ffcb06;
    font-size: 14px;
    font-weight: 400;
    line-height: 26px;
    text-transform: uppercase;
    letter-spacing: .12px;
    color: #101010;
    margin-left: 16px;
  }
}

.order-form__total-current {
  span {
    color: #333333;
    font-weight: normal;
    text-transform: uppercase;
    font-size: 17px;
  }
}

.order-form__paysystem-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 8px;
}

.order-form__paysystem-left {
  width: 35%;

  .order-form__input {
    display: none;
    width: 0;
    height: 0;
    margin: 4px 0 0;
    line-height: normal;
  }

  .order-form__label {
    position: relative;
    font-weight: normal;
    text-transform: uppercase;
    margin-left: 20px;
    padding-left: 30px;
    font-size: 15px;
    width: 445px;
    max-width: 100%;
    line-height: 1.42857143;
    color: #333333;
    display: block;
    cursor: pointer;

    img {
      float: left;
      margin-right: 5px;
      margin-left: 10px;
    }

    small {
      display: block;
      font-size: 85%;

      a {
        color: @supernova;
        text-decoration: none;
        transition: all 0.2s;

        &:hover {
          color: @supernova - 20%;
        }
      }
    }

    &:before {
      background: url("../icons/checkbox.png") -1px -1px no-repeat;
      content: "";
      width: 20px;
      height: 20px;
      position: absolute;
      top: -5px;
      left: -31px;
    }

  }

  .order-form__input[type="radio"] + label:before {
    top: 0;
    left: 0;
  }

  .order-form__input[type="radio"]:checked + label:before {
    background-position: -1px -22px;
  }
}

.order-form__paysystem-right {
  width: 55%;
  display: flex;

  img {
    max-width: 100%;
    height: auto;
  }
}

.order-form__paysystem-item {
  margin: 21px 0 10px 0;

  button {
    width: 200px;
    height: 34px;
    border: 1px solid #ffcb06;
    background-color: #ffcb06;
    font-size: 14px;
    font-weight: 400;
    line-height: 26px;
    text-transform: uppercase;
    letter-spacing: .12px;
    color: #101010;
    margin-top: 20px;
    margin-bottom: 16px;
  }
}

.order-form__privacy-policy {
  margin: 0 0 10px;
  line-height: 1.42857143;
  color: #333333;
  font-size: 16px;

  a {
    color: @supernova;
    text-decoration: none;
    transition: all 0.2s;

    &:hover {
      color: @supernova - 20%;
    }
  }
}

.order-form__phone .iti, .order-form__date .iti {
  width: 100%;
}

@media (min-width: @touch-width) {
  button {
    cursor: pointer;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

@media (max-width: 1140px) {
  .order-form__main {
    flex-flow: column;
  }

  .order-form__main-item {
    width: 100%;
  }
}

@media (max-width: 1024px) {
  .order-form__total {
    display: block;

    .order-form__discount {
      width: 100%;
      padding-left: 0;
      padding-bottom: 20px;
    }

    .order-form__total-current {
      width: 100%;
      justify-content: flex-start;
    }
  }
}

@media (max-width: 768px) {
  .order-form__confirm-row_heading {
    display: none;
  }

  .order-form__confirm-row {
    position: relative;
    flex-flow: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 5px;

    .order-form__confirm-del {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 10px;
    }
  }

  .order-form__paysystem-item {
    flex-flow: column;
  }

  .order-form__paysystem-left,
  .order-form__paysystem-right {
    width: 100%;
  }

  .order-form__paysystem-right {
    margin-top: 15px;
  }
}


@media (max-width: 580px) {
  .order-form__field {
    flex-flow: column;

    .order-form__label {
      width: 100%;
      padding-left: 0;
    }

    .order-form__input,
    .order-form__textarea,
    .order-form__phone,
    .order-form__city,
    .order-form__notes {
      width: 100%;
    }
  }

  .order-form__field_country,
  .order-form__field_city,
  .order-form__field_date {
    flex-flow: wrap;

    .order-form__label {
      width: 50%;
    }
  }
}


@media (max-width: 480px) {
  .order-form__discount {
    flex-flow: column;

    > * {
      margin: 10px 0;
    }
  }

  .order-form__total {
    .order-form__total-current {
      justify-content: center;
    }
  }

}

@media (max-width: 380px) {
  .order-form__top {
    .order-form__label {
      font-size: 13px;
    }
  }
}
