.cart {
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 56px;
  padding-right: 15px;
  padding-left: 15px;

  max-width: 1440px;
}

.cart__item {
  display: flex;
  position: relative;
  width: 100%;
  padding: 23px 0 16px 0;
  margin-bottom: 16px;
  border-top: 2px solid @supernova;
}

.cart__img {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  min-height: 166px;
  padding-right: 5px;

  img {
    max-width: 100%;
    max-height: 100%;
  }
}

.cart__item-del {
  width: 40px;
  height: 40px;
  border: 2px solid @supernova;
  position: absolute;
  top: -2px;
  right: 0;
  background-color: @white;
  background-image: url("../img/close.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 16px 16px;
  text-align: center;
  font-size: 0;
  font-weight: normal;
  text-transform: uppercase;
  padding-top: 5px;
}

.cart__price {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.cart__current-price {
  margin-top: 23px;
  text-align: right;
  color: @forest-green;
  font-weight: normal;
  font-size: 23px;
}

.cart__description {
  width: 60%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  padding-right: 20px;

  textarea {
    font-size: inherit;
    line-height: inherit;
    width: 100%;
    height: 64px;
    resize: none;
    padding: 8px 12px;
    overflow: hidden;
    display: block;
    margin: 6px 0 12px 0;
    border-color: #b2b2b2;
    transition: all 0.2s;

    &:focus {
      border-color: #b2b2b2 - 50%;
    }
  }
}

.cart__title {
  margin-top: 17px;
  margin-bottom: 4px;
  font-size: 20px;
  font-weight: 600;
  line-height: 26px;
  text-transform: uppercase
}

.cart__article {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 16px;
  font-weight: 400;
  color: @ironside-gray;
  font-style: italic;
}

.cart__item-info {
  color: @cod-gray;
  font-weight: 300;
  letter-spacing: .16px;
  line-height: 18px;

  &_small {
    font-size: 14px;
    font-style: italic;
    line-height: 16px;
    color: @ironside-gray;
  }
}

@media (min-width: @touch-width) {
  .cart__item-del {
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: @supernova;
    }

    &:active {
      opacity: 0.7;
    }
  }
}

@media (max-width: 768px) {
  .cart__item {
    display: block;
  }

  .cart__img,
  .cart__description,
  .cart__price {
    width: 100%;
    text-align: center;
    margin: 10px 0;
    padding: 0;
    border: none;
  }

  .cart__current-price {
    margin: 0 auto;
  }
}
