.goods {
  .container(@desktop-width);
}

.goods__list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding-left: 0;
  margin: 0;
  list-style: none;
  justify-content: space-around;

  &::after {
    content: "";
    width: 0;
    flex: auto;
  }
 }

.goods__item {
  width: 33.3333%;
  flex: none;
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-sizing: border-box;
}

.goods__image {
  display: flex;
  width: 100%;
  flex: auto;
  margin-bottom: 40px;

  img {
    max-width: 100%;
    object-fit: contain;
  }
}

.goods__title {
  margin-top: 0;
  margin-bottom: 6px;
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
  text-transform: uppercase;
  color: @ironside-gray;
  text-decoration: none;
}

.goods__price {
  margin-top: 0;
  margin-bottom: 6px;
  font-size: 14px;
  font-weight: 600;
  line-height: 26px;
  color: @alpine;
}

.goods__button {
  width: 200px;
  height: 32px;
  border: 1px solid @supernova;
  background-color: @supernova;
  font-size: 12px;
  font-weight: 400;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 0.12px;
  color: @cod-gray;
}

.goods__pagination {
  margin-top: 70px;
  margin-bottom: 140px;
}

@media (min-width: @touch-width) {
  .goods__button {
    cursor: pointer;

    &:hover,
    &:focus {
      background-color: fade(@supernova, 70%);
      border-color: fade(@supernova, 70%);
    }

    &:active {
      background-color: fade(@supernova, 30%);
      border-color: fade(@supernova, 30%);
    }
  }
}

@media (max-width: @tablet-width) {
  .goods__item {
    width: 50%;
  }
}

@media (max-width: 576px) {
  .goods__list {
    flex-direction: column;
    align-items: center;
  }

  .goods__item {
    width: 100%;
  }
}
